<style>
  /*不显示竖向Menu的边栏*/
  .ivu-menu-vertical.ivu-menu-light:after{
    width: 0px;
  }
  .leftPanel{
    overflow: auto;
  }
  .rightPanel{
    display: flex;
    display: -webkit-flex;
    -webkit-box-orient: vertical;
    -webkit-flex-flow: column;
    flex-flow: column;
    padding: 10px;
  }
</style>
<template>
  <Row type="flex" class="fullWidth">
    <Col span="4" class="leftPanel border-shadow">
    <Menu activeName="page1" class="left-menu" width="auto" openNames="['unitsmanager']" @on-select="onNaviMenuClick">
      <Submenu name="unitsmanager">
        <template slot="title">
          <Icon type="ios-navigate"></Icon>
          <span>单位管理</span>
       </template>
        <Menu-item name="page1">左侧导航1</Menu-item>
        <Menu-item name="page2">左侧导航2</Menu-item>
      </Submenu>
    </Menu>
    </Col>
    <Col span="20" class="rightPanel">
    <router-view></router-view>
    </Col>
  </Row>
</template>

<script>
  import config from '../../config/config'
  export default {
    data(){
      return {};
    },
    methods: {
      fetchData: function(){
        document.title = '单位管理 - ' + config.web_name;
        this.$store.commit('setActiveNaviMenu', 'superAdmin');
      },
      onNaviMenuClick: function(key){
        //Route to sub module
        this.$router.push(key);
      }
    },
    mounted(){
      let self = this;
      self.fetchData();
    }

  }
</script>



